import React, { useEffect } from "react";
import { Routes, Route, Outlet, Navigate, HashRouter } from "react-router-dom";
import ErrorPage from "./pages/ErrorPage/ErrorPage";
import HelpPage from "./pages/HelpPage/HelpPage";
import DashboardPage from "./pages/DashboardPage/DashboardPage";
import RoomsPage from "./pages/RoomsPage/RoomsPage";
import MeetingsPage from "./pages/MeetingsPage/MeetingsPage";
import ScreenInfoPage from "./pages/ScreenInfoPage/ScreenInfoPage";
import ScreenPage from "./pages/ScreenPage/ScreenPage";
import ScreensPage from "./pages/ScreensPage/ScreensPage";
import SettingsPage from "./pages/SettingsPage/SettingsPage";
import EmployeeInfoPage from "./pages/EmployeeInfoPage/EmployeeInfoPage";
import EmployeeresPage from "./pages/EmployeeresPage/EmployeeresPage";
import Header from "./layouts/Header/Header";
import Sidebar from "./layouts/Sidebar/Sidebar";
import Footer from "./layouts/Footer/Footer";
import "./sass/App.css";
import ModalWindow from "./layouts/ModalWindow/ModalWindow";
import MeetingInfoPage from "./pages/MeetingInfoPage/MeetingInfoPage";
import RoomInfoPage from "./pages/RoomInfoPage/RoomInfoPage";
import ErrorFullPage from "./pages/ErrorFullPage/ErrorFullPage";
import { PLATFORM_THEME_LSTORAGE_NAME } from "./data/staticPlatformData";

function App() {

  const PlatformAppLayout = (): JSX.Element => (
    <div className="container">
      <ModalWindow/>
      <Header/>
      <Sidebar/>
      <Outlet/>
      <Footer/>
    </div>
  );

  useEffect((): void => {
    const localStoragePlatformTheme: string | null = localStorage.getItem(PLATFORM_THEME_LSTORAGE_NAME);

    if (localStoragePlatformTheme) {
      const themeValue: string = JSON.parse(localStoragePlatformTheme);
      document.documentElement.setAttribute("data-theme", themeValue);

    } else {
      document.documentElement.setAttribute("data-theme", "dark");
    }
  }, []);

  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Navigate to="/platform"/>}/>
        <Route element={<PlatformAppLayout/>}>
          <Route path="/platform" element={<DashboardPage />} />
          <Route path="platform/meetings" element={<MeetingsPage />} />
          <Route path="platform/meetings/:value" element={<MeetingsPage />} />
          <Route path="platform/meetings/meeting/:id" element={<MeetingInfoPage />} />
          <Route path="platform/rooms" element={<RoomsPage />} />
          <Route path="platform/rooms/room/:id" element={<RoomInfoPage />} />
          <Route path="platform/employeeres" element={<EmployeeresPage />} />
          <Route path="platform/employeeres/employee/:id" element={<EmployeeInfoPage />} />
          <Route path="platform/screens" element={<ScreensPage />} />
          <Route path="platform/screens/screen/:id" element={<ScreenInfoPage />} />
          <Route path="platform/settings" element={<SettingsPage />} />
          <Route path="platform/help" element={<HelpPage />} />
          <Route path="platform/*" element={<ErrorPage />} />
        </Route>
        <Route path="/screen/:id" element={<ScreenPage />} />
        <Route path="*" element={<ErrorFullPage />} />
      </Routes>
    </HashRouter>
  );
}

export default App;
